#root {
    height: 85%;
}

.aboutme {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.aboutme-title {
    font-size: 60px;
    text-shadow: 3px 3px 2px #E0AFA1;
    color: #463F3A;
    line-height: 1.2;
    visibility: hidden;
    animation: 1s ease-in-out 0.2s 1 forwards slideUp;
    margin-top: 20px;
    margin-bottom: 20px;
}

.aboutme-photo-container {
    margin-top: 20px;
    animation: 1s ease-in-out 0s 1 forwards slideUp;
}

.aboutme-photo {
    width: 250px;
}

@keyframes slideUp {
    0% {
        visibility: visible;
        opacity: 0;
        transform: translateY(15px);
    }

    100% {
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
    }
}